.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css?family=Share&display=swap');

.S [data-kind="digit"],
.M [data-kind="digit"],
.L [data-kind="digit"],
.XL [data-kind="digit"] {
  font-family: 'Share', sans-serif;
  width: 1.1ch;
}

.S [data-kind="digit"][data-mode="alpha"],
.M [data-kind="digit"][data-mode="alpha"],
.L [data-kind="digit"][data-mode="alpha"],
.XL [data-kind="digit"][data-mode="alpha"] {
  width: 1.35ch;
}

.S [data-kind="digit"][data-mode="words"],
.M [data-kind="digit"][data-mode="words"],
.L [data-kind="digit"][data-mode="words"],
.XL [data-kind="digit"][data-mode="words"] {
  width: 100%;
}

.S [data-kind="digit"] {
  font-size: 18px;
  line-height: 19px;
}

.M [data-kind="digit"] {
  font-size: 30px;
  line-height: 32px;
}

.L [data-kind="digit"] {
  font-size: 48px;
  line-height: 52px;
}

.XL [data-kind="digit"] {
  font-size: 78px;
  line-height: 82px;
}

.light [data-kind="digit"],
.lightBordered [data-kind="digit"] {
  color: #333;
  background-color: #EEE;
}

.lightBordered [data-kind="digit"] {
  border: 1px solid #DDD;
  border-radius: 2px;
  margin-right: 1px;
}

.lightBordered [data-kind="digit"]:last-child {
  margin-right: 0;
}

.dark [data-kind="digit"],
.darkBordered [data-kind="digit"] {
  color: #EEE;
  background-color: #333;
}

.darkBordered [data-kind="digit"] {
  border: 1px solid #666;
  border-radius: 2px;
  margin-right: 1px;
}

.darkBordered [data-kind="digit"]:last-child {
  margin-right: 0;
}

.container-fluid {
  padding-top: 25px;
}

.center-title {
  text-align: center
}